@import "reset.scss";
@import "typography.scss";
@import "layout.scss";

/*Sets default style of every button on page*/
button{
  border: none;
  font-size: 0px;
}

button:hover{
  cursor: pointer;
}

.grid-container {
  display: grid; 
  grid-template-columns: 1fr; 
  grid-template-rows: 15% 1fr 15%; 
  grid-template-areas: 
    "header"
    "main"
    "musicplayer"; 

  /*Forces grid to cover entire screen*/
  width: 100wh; 
  height: 100vh;
}

/*header css*/
.header {
  grid-area: header;
  background-color: #0B6E4F;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header img{
  height: 10vh;
  padding-left: 3vw;
}

.header .navlinks{
  width: 40vw;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}

.header a{
  color: white;
  font-weight: 550; /* 400 is normal, 700 is bold*/
  font-size: 1.2em;
  text-decoration: none; /*Removes underline from link*/
}

.header a:hover{
  color: #EBA640;
  transition: 0.2s;
}


/*main css*/
.main {
  grid-area: main;
  padding: 2vh;
  padding-left: 3vw;
}

.main h1{
  padding-left: 4vw;
  padding-bottom: 2vh;
  font-size: 2.5em;
  font-weight: 700;
}

/*Makes sure task columns go across in a row*/
.taskContent{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.taskColumn{
  position: relative; /*Makes this the parent container for taskOut button position*/
  display: flex;
  flex-direction: column;
  align-items: center;
  /*min-height lets the div auto resize itself based on content*/
  min-height: 35vh;
  width: 21vw;
  background-color: #ED9D28;
  border: 2px solid black;
  border-radius: 10px;
  padding-bottom: 2vh;
  margin-right: 3vw;
  margin-bottom: 5vh;
}

.taskOut{
  position: absolute; 
  left: 18.5vw;
  top: 1vh;
  background-color: transparent;
}

.taskOut .icon{
  height: 3vh;
}

.taskCard{
  /*
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  */
  min-height: 20vh;
  width: 19vw;
  background-color: white;
  border: 0.1px solid black;
  border-radius: 15px;
  margin-bottom: 2vh;
}

.taskAdd{
  height: 5vh;
  width: 19vw;
  background-color: white;
  border: 0.1px solid black;
  border-radius: 10px;
}

.taskCardTitle{
  display: flex;
  justify-content: center;
  background-color: black;
  border-radius: 13px 13px 0px 0px;
}

.taskCardTitle h3{
  color: white;
}

.taskCardInfo{
  padding: 1vh;
}

.taskCardInfo p{
  font-weight: 550;
  margin-bottom: 1vh;
}

/*Colors the color of the priority ratings*/
.pHigh{
  color: #B32323;
}

.pMedium{
  color: #183FBD;
}

.pLow{
  color: #0B6E4F;
}

.taskCardIcons{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.taskCard button{
  margin-top: 1vh;
  margin-left: 1vw;
  margin-right: 1vw;
  margin-bottom: 2vh;
}

.icon{
  height: 4vh;
}

.taskAdd{
  display: flex;
  justify-content: center;
  align-items: center;
}

.taskAdd .icon{
  height: 3vh;
}

.taskColumn h2{
  padding: 1vw;
  font-size: 1.5em;
  font-weight: 550;
}

.taskColumnAdd{
  width: 21vw;
  height: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  border-radius: 10px;
  margin-bottom: 3vh;
}

.taskColumnAdd .icon{
  height: 3vh;
}

.newTaskForm{
  /*display: none*/
  position: fixed;
  top: 50vh;
  left: 50vw;
  margin-top: -25vh; /* Negative half of height. */
  margin-left: -15vw; /* Negative half of width. */
  width: 30vw;
  min-height: 50vh;
  background-color: white;
  border: 2px solid #0B6E4F;
  padding: 2.5vw;
}

.newTaskOut{
  position: absolute; 
  left: 27vw;
  top: 2vh;
  background-color: transparent;
}

.newTaskOut .icon{
  height: 3vh;
}

label{
  font-weight: 550;
}

input{
  margin-bottom: 2vh;
}

select{
  margin-bottom: 2vh;
}

.newTaskForm h2{
  text-align: center;
  margin-bottom: 2vh;
}

.fullIn{
  width: 25vw;
}

.addSubmit{
  display: block;
  background: #EBA640;
  color: black;
  font-weight: 550;
  
  border-radius: 25px;
  width: 7vw;
  height: 5vh;
  margin: auto;
  margin-top: 2vh;
}



/*music player css*/
.musicPlayer {
  grid-area: musicplayer;
  background-color: black;
}





@media only screen and (max-width: 450px){

  .header a{
    font-size: 0em;
  }
  
  .main{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .main h1{
    padding: 0;
  }
  .taskContent{
    flex-direction: column;
    justify-content: center;
  }
  
  .taskColumn{
    width: 85vw;
    margin-right: 0vw;
  }

  .taskOut{
    left: 75vw;
  }

  .taskColumn h2{
    margin: 2vw;
  }
  
  .taskCard{
    width: 80vw;
  }

  .taskCard button{
    border: none;
    margin-top: 1vh;
    margin-left: 3vw;
    margin-right: 3vw;
    margin-bottom: 2vh;
  }

  .taskAdd{
    width: 80vw;
  }

  .taskColumnAdd{
    width: 85vw;
  }

  .newTaskForm{
    width: 80vw;
    margin-left: -42vw;
  }

  .fullIn{
    width: 72vw;
  }

  .newTaskOut{
  left: 72vw;
  top: 1vh;
  }

  .addSubmit{
    width: 20vw;
  }
  
}